/* sass */
.box-shadow {
  background: #fff;
  margin: auto;
  padding: 0.5em 1em;
  box-shadow: 0 5px 30px rgba(0, 0, 0, 0.2);
  border-radius: 2px;
}

/* https://codepen.io/lucasyem/pen/ZEEYKdj */
.AddTodo_form__group__rQopU {
  position: relative;
  padding: 15px 0 0;
  margin-top: 10px;
  width: 50%;
}

.AddTodo_form__field__3h3Ds {
  font-family: inherit;
  width: 100%;
  border: 0;
  border-bottom: 2px solid #9b9b9b;
  outline: 0;
  font-size: 1.3rem;
  color: #fff;
  padding: 7px 0;
  background: transparent;
  transition: border-color 0.2s;
}
.AddTodo_form__field__3h3Ds::-webkit-input-placeholder {
  color: transparent;
}
.AddTodo_form__field__3h3Ds:-ms-input-placeholder {
  color: transparent;
}
.AddTodo_form__field__3h3Ds::placeholder {
  color: transparent;
}
.AddTodo_form__field__3h3Ds:-ms-input-placeholder ~ .AddTodo_form__label__16aBm {
  font-size: 1.3rem;
  cursor: text;
  top: 20px;
}
.AddTodo_form__field__3h3Ds:placeholder-shown ~ .AddTodo_form__label__16aBm {
  font-size: 1.3rem;
  cursor: text;
  top: 20px;
}

.AddTodo_form__label__16aBm {
  position: absolute;
  top: 0;
  display: block;
  transition: 0.2s;
  font-size: 1rem;
  color: #9b9b9b;
}

.AddTodo_form__field__3h3Ds:focus {
  padding-bottom: 6px;
  font-weight: 700;
  border-width: 3px;
  border-image: linear-gradient(to right, #11998e, #38ef7d);
  border-image-slice: 1;
}
.AddTodo_form__field__3h3Ds:focus ~ .AddTodo_form__label__16aBm {
  position: absolute;
  top: 0;
  display: block;
  transition: 0.2s;
  font-size: 1rem;
  color: #11998e;
  font-weight: 700;
}

/* reset input */
.AddTodo_form__field__3h3Ds:required,
.AddTodo_form__field__3h3Ds:invalid {
  box-shadow: none;
}

